import {Component, Input} from '@angular/core';
import {ScoreService} from '../../service/score.service';
import {MatDialog} from '@angular/material/dialog';
import {BravoCardComponent} from '../bravocard/bravo-card.component';
import {DialogService} from '../../service/dialog.service';

@Component({
  selector: 'app-quizz',
  templateUrl: './quiz.component.html',
  styleUrls: ['./quiz.component.scss']
})
export class QuizComponent {
  questionPool: Question[] = [
    {
      question: 'Comment est votre blanquette ?',
      answers: [
        {text: 'La blanquette est bonne', valid: true},
        {text: 'Ma quoi ?'},
        {text: 'Trop chère.'},
        {text: 'Froide.'}
      ]
    },
    {
      question: 'Quelle pillule Néo a-t-il consomé ?',
      answers: [
        {text: 'La rouge', valid: true},
        {text: 'La bleue'},
        {text: 'TOUTES'},
        {text: 'La verte'}
      ]
    },
    {
      question: 'Where is Brian ?',
      answers: [
        {text: 'Brian is in the kitchen', valid: true},
        {text: 'Who is Brian ?'},
        {text: 'Brian ! SPOOOON !'},
        {text: 'Brian is studying in his room'}
      ]
    },
    {
      question: 'Where is Brian ?',
      answers: [
        {text: 'Brian is in the kitchen', valid: true},
        {text: 'Who is Brian ?'},
        {text: 'Brian ! SPOOOON !'},
        {text: 'Brian is studying in his room'}
      ]
    },
  ];
  @Input() question: Question;

  constructor(private scoreService: ScoreService, public dialog: MatDialog, public dialogService: DialogService) {
    this.pickQuestion();
  }

  pickQuestion() {
    this.question = this.questionPool[Math.floor(this.questionPool.length * Math.random())];
    this.question.answers = this.shuffle(this.question.answers);
    this.question.answers.forEach(item => item.color = this.getRandomColor());
  }

  verify(valid?: boolean) {
    valid ? this.correctAnswer() : this.wrongAnswer();
  }

  correctAnswer() {
    this.scoreService.incrementScore();
    const dialogRef = this.dialog.open(BravoCardComponent, {
      width: '50%'
    });

    const closeButtonSubscription = this.dialogService.quitEvent.subscribe(() => {
      console.log("quitEvent");
      dialogRef.close();
    });

    dialogRef.afterClosed().subscribe(result => {
      this.pickQuestion();
      closeButtonSubscription.unsubscribe();
    });
  }

  wrongAnswer() {
    console.log('wrongAnswer');
  }

  shuffle(a: Answer[]): any {
    for (let i = a.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [a[i], a[j]] = [a[j], a[i]];
    }
    return a;
  }

  getRandomColor() {
    const letters = '0123456789ABCDEF';
    let color = '#';
    for (let i = 0; i < 6; i++) {
      color += letters[Math.floor(Math.random() * 16)];
    }
    return color;
  }

  lightOrDark(color) {
    if (color) {
      const r = parseInt(color.substring(1, 3), 16);
      const g = parseInt(color.substring(3, 5), 16);
      const b = parseInt(color.substring(5, 7), 16);

      // HSP (Highly Sensitive Poo) equation from http://alienryderflex.com/hsp.html
      const hsp = Math.sqrt(
        0.299 * (r * r) +
        0.587 * (g * g) +
        0.114 * (b * b)
      );

      // Using the HSP value, determine whether the color is light or dark
      if (hsp > 127.5) {
        return 'light';
      } else {

        return 'dark';
      }
    }
  }

}


interface Answer {
  text: string;
  valid?: boolean;
  color?: string;
}

interface Question {
  question: string;
  answers: [Answer, Answer, Answer, Answer];
}

